.home {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 40px;
  height: 100vh;
  font-weight: 750;
}

.home-title{
  font-size: 30px;
}

.home-name{
 font-size: 80px
}

.home-text{
  font-weight: 600;
  font-size: 25px;
}

a {
  color: #6e6e6e;
  margin-top: 20px;
  text-decoration: none;
}

a:hover {
  color: #7a87c5;
}

@media only screen and (max-width: 600px) {
  .home {
    flex-direction: column;
    align-items: flex-start;
    padding: 2em;
    height: auto;
  }

  .home-title {
    font-size: 20px;
  }

  .home-name {
    font-size: 50px;
  }

  .home-text {
    font-size: 16px;
  }
}